// Scoped styles for the Orbital Rendezvous Simulator

#orbit-container {
 max-width: 1400px;
 margin: 2rem auto;
 padding: 1.5rem;
 background: linear-gradient(135deg, var(--global-bg-color) 0%, rgba(15, 23, 42, 0.8) 100%);
 border-radius: 20px;
 box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.1);
 backdrop-filter: blur(10px);
 border: 1px solid rgba(255, 255, 255, 0.1);
}

#orbit-container * {
 box-sizing: border-box;
}

#orbit-controls {
 background: linear-gradient(135deg, rgba(30, 41, 59, 0.9) 0%, rgba(15, 23, 42, 0.95) 100%);
 border: 1px solid rgba(59, 130, 246, 0.3);
 border-radius: 16px;
 padding: 1.5rem;
 margin-bottom: 1.5rem;
 display: grid;
 grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
 gap: 1.5rem;
 align-items: center;
 box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

#orbit-container .control-group {
 display: flex;
 flex-direction: column;
 gap: 0.75rem;
 align-items: center;
}

#orbit-container .control-group label {
 font-size: 0.9rem;
 font-weight: 600;
 color: rgba(255, 255, 255, 0.9);
 text-align: center;
 text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
 letter-spacing: 0.5px;
}

#orbit-container .control-row {
 display: flex;
 gap: 0.75rem;
 align-items: center;
 justify-content: center;
}

#orbit-container .burn-button {
 background: linear-gradient(135deg, var(--global-theme-color) 0%, rgba(59, 130, 246, 0.8) 100%);
 color: white;
 border: none;
 padding: 0.875rem 1.25rem;
 border-radius: 10px;
 cursor: pointer;
 font-weight: 600;
 font-size: 0.95rem;
 transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
 min-width: 70px;
 position: relative;
 overflow: hidden;
 box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
 text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}

#orbit-container .burn-button::before {
 content: '';
 position: absolute;
 top: 0;
 left: -100%;
 width: 100%;
 height: 100%;
 background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
 transition: left 0.5s;
}

#orbit-container .burn-button:hover::before { left: 100%; }

#orbit-container .burn-button:hover {
 transform: translateY(-2px) scale(1.05);
 box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3), 0 0 20px rgba(59, 130, 246, 0.4);
}

#orbit-container .burn-button:active { transform: translateY(0) scale(0.98); }

#orbit-container .burn-button.prograde {
 background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
 box-shadow: 0 4px 15px rgba(34, 197, 94, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.1);
}
#orbit-container .burn-button.prograde:hover {
 box-shadow: 0 8px 25px rgba(34, 197, 94, 0.4), 0 0 20px rgba(34, 197, 94, 0.6);
}

#orbit-container .burn-button.retrograde {
 background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
 box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.1);
}
#orbit-container .burn-button.retrograde:hover { box-shadow: 0 8px 25px rgba(239, 68, 68, 0.4), 0 0 20px rgba(239, 68, 68, 0.6); }

#orbit-container .burn-button.radial-out {
 background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
 box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.1);
}
#orbit-container .burn-button.radial-out:hover { box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4), 0 0 20px rgba(59, 130, 246, 0.6); }

#orbit-container .burn-button.radial-in {
 background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
 box-shadow: 0 4px 15px rgba(245, 158, 11, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.1);
}
#orbit-container .burn-button.radial-in:hover { box-shadow: 0 8px 25px rgba(245, 158, 11, 0.4), 0 0 20px rgba(245, 158, 11, 0.6); }

#orbit-container button {
 background: linear-gradient(135deg, var(--global-theme-color) 0%, rgba(59, 130, 246, 0.8) 100%);
 color: white;
 border: none;
 padding: 0.75rem 1.25rem;
 border-radius: 8px;
 cursor: pointer;
 font-weight: 500;
 transition: all 0.3s ease;
 box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
}

#orbit-container button:hover {
 transform: translateY(-1px);
 box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3), 0 0 20px rgba(59, 130, 246, 0.4);
}

#orbit-container select {
 background: linear-gradient(135deg, rgba(30, 41, 59, 0.9) 0%, rgba(15, 23, 42, 0.9) 100%);
 color: var(--global-text-color);
 border: 1px solid rgba(59, 130, 246, 0.3);
 padding: 0.75rem;
 border-radius: 8px;
 cursor: pointer;
 transition: all 0.3s ease;
}

#orbit-container select:hover {
 border-color: rgba(59, 130, 246, 0.6);
 box-shadow: 0 0 15px rgba(59, 130, 246, 0.3);
}

#orbit-container .slider-group {
 display: flex;
 flex-direction: column;
 align-items: center;
 gap: 0.5rem;
}

#orbit-container .slider-group label {
 font-size: 0.9rem;
 font-weight: 600;
 color: rgba(255, 255, 255, 0.9);
 text-align: center;
 text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}

#orbit-container .slider-group input[type="range"] {
 width: 140px;
 height: 6px;
 background: linear-gradient(90deg, rgba(59, 130, 246, 0.3) 0%, rgba(59, 130, 246, 0.8) 100%);
 border-radius: 3px;
 outline: none;
 -webkit-appearance: none;
}

#orbit-container .slider-group input[type="range"]::-webkit-slider-thumb {
 -webkit-appearance: none;
 width: 18px;
 height: 18px;
 background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
 border-radius: 50%;
 cursor: pointer;
 box-shadow: 0 2px 8px rgba(59, 130, 246, 0.5);
}

#orbit-container .slider-group input[type="range"]::-moz-range-thumb {
 width: 18px;
 height: 18px;
 background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
 border-radius: 50%;
 cursor: pointer;
 border: none;
 box-shadow: 0 2px 8px rgba(59, 130, 246, 0.5);
}

#orbit-viz-container {
 position: relative;
 width: 100%;
 aspect-ratio: 16/9;
 background: radial-gradient(ellipse at center, #0f172a 0%, #000000 70%);
 border: 1px solid rgba(59, 130, 246, 0.3);
 border-radius: 16px;
 overflow: hidden;
 margin-bottom: 1.5rem;
 box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

#orbit-viz { width: 100%; height: 100%; }

#orbit-hud {
 background: linear-gradient(135deg, rgba(30, 41, 59, 0.9) 0%, rgba(15, 23, 42, 0.95) 100%);
 border: 1px solid rgba(59, 130, 246, 0.3);
 border-radius: 16px;
 padding: 1.5rem;
 display: grid;
 grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
 gap: 1.5rem;
 box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

#orbit-container .hud-panel {
 background: linear-gradient(135deg, rgba(59, 130, 246, 0.1) 0%, rgba(30, 58, 138, 0.05) 100%);
 border: 1px solid rgba(59, 130, 246, 0.3);
 border-radius: 12px;
 padding: 1rem;
 transition: all 0.3s ease;
 box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

#orbit-container .hud-panel:hover {
 border-color: rgba(59, 130, 246, 0.5);
 box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2), 0 0 15px rgba(59, 130, 246, 0.2);
 transform: translateY(-2px);
}

#orbit-container .hud-panel h4 {
 color: #60a5fa;
 margin-bottom: 0.75rem;
 font-size: 1rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.75px;
 text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
 border-bottom: 2px solid rgba(59, 130, 246, 0.3);
 padding-bottom: 0.5rem;
}

#orbit-container .hud-value {
 font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
 font-size: 0.9rem;
 color: var(--global-text-color);
 display: flex;
 justify-content: space-between;
 margin-bottom: 0.5rem;
 padding: 0.25rem 0;
 border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

#orbit-container .hud-value:last-child { border-bottom: none; }
#orbit-container .hud-value .label { color: rgba(255, 255, 255, 0.7); font-weight: 400; }
#orbit-container .hud-value .value { color: #60a5fa; font-weight: 600; text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5); }

#orbit-status {
 font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
 font-size: 1rem;
 color: #60a5fa;
 padding: 1rem;
 background: linear-gradient(135deg, rgba(59, 130, 246, 0.1) 0%, rgba(30, 58, 138, 0.05) 100%);
 border: 1px solid rgba(59, 130, 246, 0.3);
 border-radius: 12px;
 text-align: center;
 margin-top: 1rem;
 box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
 text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}

#orbit-container .scenario-button {
 background: linear-gradient(135deg, var(--global-theme-color) 0%, rgba(59, 130, 246, 0.8) 100%);
 color: white;
 border: none;
 padding: 0.75rem 1rem;
 border-radius: 8px;
 cursor: pointer;
 font-size: 0.85rem;
 font-weight: 500;
 transition: all 0.3s ease;
 box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
}

#orbit-container .scenario-button:hover {
 transform: translateY(-1px) scale(1.02);
 box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3), 0 0 15px rgba(59, 130, 246, 0.4);
}

#orbit-container .burn-info {
 font-size: 0.75rem;
 color: rgba(255, 255, 255, 0.7);
 text-align: center;
 margin-bottom: 0.5rem;
 padding: 0.5rem;
 background: rgba(59, 130, 246, 0.1);
 border-radius: 6px;
 border: 1px solid rgba(59, 130, 246, 0.2);
}

#orbit-container .scenario-grid {
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 gap: 0.5rem;
 width: 100%;
}

#orbit-container .scenario-grid .scenario-button {
 padding: 0.6rem 0.4rem;
 font-size: 0.75rem;
 line-height: 1.2;
 min-height: 60px;
 display: flex;
 align-items: center;
 justify-content: center;
 text-align: center;
}

/* Effects */
.burn-effect { position: absolute; pointer-events: none; border-radius: 50%; animation: burnPulse 0.6s ease-out; }
@keyframes burnPulse { 0% { transform: scale(0); opacity: 1; } 100% { transform: scale(3); opacity: 0; } }

@media (max-width: 768px) {
 #orbit-container { margin: 1rem; padding: 1rem; }
 #orbit-controls { grid-template-columns: 1fr; gap: 1rem; padding: 1rem; }
 #orbit-container .control-group { align-items: stretch; }
 #orbit-container .control-row { justify-content: stretch; }
 #orbit-container .burn-button { flex: 1; padding: 1rem; }
 #orbit-hud { grid-template-columns: 1fr; padding: 1rem; }
}

.no-webgl {
 display: flex;
 align-items: center;
 justify-content: center;
 height: 100%;
 font-size: 1.2rem;
 color: rgba(255, 255, 255, 0.7);
 text-align: center;
 padding: 2rem;
 background: linear-gradient(135deg, rgba(239, 68, 68, 0.1) 0%, rgba(220, 38, 38, 0.1) 100%);
 border: 1px solid rgba(239, 68, 68, 0.3);
 border-radius: 12px;
}

/* Loading */
.loading {
 position: absolute;
 top: 50%; left: 50%; transform: translate(-50%, -50%);
 color: #60a5fa; font-size: 1.2rem; font-weight: 600; text-shadow: 0 1px 2px rgba(0,0,0,0.5);
}
.loading::after { content: ''; animation: loadingDots 1.5s infinite; }
@keyframes loadingDots { 0%, 20% { content: '.'; } 40% { content: '..'; } 60%, 100% { content: '...'; } }

.units-legend {
 margin: 0 0 1rem 0;
 padding: 0.75rem 1rem;
 font-size: 0.9rem;
 color: rgba(255,255,255,0.85);
 background: linear-gradient(135deg, rgba(59,130,246,0.08) 0%, rgba(15,23,42,0.3) 100%);
 border: 1px solid rgba(59,130,246,0.25);
 border-radius: 10px;
}

/* Toast messages (scoped class) */
.orbit-toast {
 position: fixed; top: 72px; right: 20px; max-width: 400px;
 padding: 1rem 1.25rem;
 background: linear-gradient(135deg, rgba(59, 130, 246, 0.95) 0%, rgba(30, 58, 138, 0.98) 100%);
 border: 1px solid rgba(59, 130, 246, 0.8);
 border-radius: 12px; color: white; font-size: 0.95rem; font-weight: 500;
 box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4), 0 0 20px rgba(59, 130, 246, 0.6);
 transform: translateX(450px);
 transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
 z-index: 1100; backdrop-filter: blur(10px); text-shadow: 0 1px 2px rgba(0,0,0,0.5);
}
.orbit-toast.show { transform: translateX(0); }
.orbit-toast .toast-icon { display: inline-block; margin-right: 0.5rem; font-size: 1.1em; }

/* Phase gauge */
.phase-gauge { background: linear-gradient(135deg, rgba(59,130,246,0.1) 0%, rgba(30,58,138,0.05) 100%);
 border: 1px solid rgba(59,130,246,0.3); border-radius: 12px; padding: 1rem; box-shadow: 0 4px 15px rgba(0,0,0,0.1); }
.phase-gauge h4 { color: #60a5fa; margin-bottom: 0.75rem; font-size: 1rem; font-weight: 600; text-transform: uppercase; letter-spacing: 0.75px; }
.phase-gauge .row { display: flex; justify-content: space-between; margin: 0.25rem 0; }
.phase-bar { position: relative; height: 10px; background: rgba(59,130,246,0.2); border-radius: 6px; overflow: hidden; margin-top: 0.5rem; }
.phase-fill { position: absolute; top: 0; left: 0; bottom: 0; width: 0%; background: linear-gradient(90deg, #22c55e, #3b82f6); transition: width 0.2s ease; }
.phase-status { margin-top: 0.5rem; text-align: center; font-weight: 600; }
.phase-status.good { color: #22c55e; } .phase-status.warn { color: #f59e0b; } .phase-status.bad { color: #ef4444; } .phase-status.na { color: #94a3b8; }

/* Keyboard shortcuts tooltip */
.keyboard-shortcuts {
 position: fixed; bottom: 20px; right: 20px; z-index: 1000;
 background: rgba(15, 23, 42, 0.9);
 color: #e5e7eb; padding: 0.75rem 1rem; border-radius: 8px;
 border: 1px solid rgba(59, 130, 246, 0.4); display: none;
}
.keyboard-shortcuts.show { display: block; }
.keyboard-shortcuts h5 { margin: 0 0 0.5rem 0; color: #93c5fd; }
.keyboard-shortcuts kbd { background: rgba(59, 130, 246, 0.2); border: 1px solid rgba(59,130,246,0.4); border-radius: 3px; padding: 0.1rem 0.3rem; font-size: 0.75rem; margin-right: 0.5rem; }

/* Prefer reduced motion: minimize animations */
@media (prefers-reduced-motion: reduce) {
 #orbit-container .burn-button:hover,
 #orbit-container button:hover {
 transform: none;
 box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 0 0 1px rgba(255, 255, 255, 0.1);
 }
 .burn-effect { animation: none; }
}
